import React, { Suspense } from "react";
import styled from "styled-components";
import { OrbitControls } from "@react-three/drei";
import { Canvas } from "@react-three/fiber";
import Cube from "./Cube";
import Whatsapp from "../asset/img/Whatsapp.glb";
import Linkedin from "../asset/img/Linkedin.glb";
import Discord from "../asset/img/Discord.glb";
import MapChart from "./Map";
const Section = styled.div`
  height: 100vh;
  color: white;
  scroll-snap-align: end;
`;
const Container = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  gap: 50px;
  @media only screen and (max-width: 768px) {
    flex-direction: column-reverse;
  }
`;
const Left = styled.div`
  flex: 1;
  width: 50vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;
const Form = styled.form`
  width: 500px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
`;
const Title = styled.h1`
  font-weight: 700;
`;
const Input = styled.input`
  border-radius: 25px;
  color:black
  width: 190px;
  padding: 12px;
  border: none;
  font-size: 18px;
  background-color: #F0F0F0;
  ::placeholder {
    color: black;
    font-size: 20px;
  }
`;
const TextArea = styled.textarea`
  border: none;
  c
  font-size: 18px;
  border-radius: 25px;
  padding: 12px;
  width: 190px;
  font-family: "Segoe UI";
  background-color: #F0F0F0;
  ::placeholder {
    color: black;
    font-size: 21px;
    font-family: "Segoe UI";
  }
  
`;
const Button = styled.button`
  display: block;
  width: 150px;
  font-size: 1.2rem;
  padding: 20px;
  cursor: pointer;
  text-align: center;
  outline: none;
  color: white;
  background-color: rebeccapurple;
  border: none;
  border-radius: 40px;
  box-shadow: 0 4px #999;
  &: hover {
    background-color: #7e3fbd;
  }

  &: active {
    background-color: #7e3fbd;
    box-shadow: 0 2px #666;
    transform: translateY(4px);
  }
`;
const Right = styled.div`
  flex: 1;
  position: relative;
`;
const ElementContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;
const Elements = styled.div`
  height: 10vh;
  width: 10vw;
`;
const ContactMe = () => {
  return (
    <Section id="Contact">
      <Container>
        <Left>
          <Form
            method="POST"
            action="https://script.google.com/macros/s/AKfycbwC5widcfhjG84AJnl9uFboyE81Jciv7jc9G7JFC3hCXQyT0D0CiST4faJ5WT0TNp_o/exec"
          >
            <Title>Contact Me</Title>
            <Input placeholder="Name" name="Name" type={"text"}></Input>
            <Input placeholder="Email" name="Email" type={"email"}></Input>
            <TextArea placeholder="Description" name="Description"></TextArea>
            <Button>Connect</Button>
          </Form>
          <Title>Connect Faster ..!</Title>
          <ElementContainer>
            <Elements>
              <a href="https://www.linkedin.com/in/aniket-andharikar-247b9218b/">
                <Canvas camera={{ position: [0, 0, 2], fov: 25 }}>
                  <Suspense fallback={null}>
                    <ambientLight intensity={0.8} />
                    <directionalLight position={[10, 10, -10]} />
                    <Cube glbfile={Linkedin} />
                    <OrbitControls enableZoom={false} autoRotate={false} />
                  </Suspense>
                </Canvas>
              </a>
            </Elements>
            <Elements>
              <a href="https://discord.gg/XGqBrzteUq">
                <Canvas camera={{ position: [0, 0, 2], fov: 25 }}>
                  <Suspense fallback={null}>
                    <ambientLight intensity={0.8} />
                    <directionalLight position={[10, 10, -10]} />
                    <Cube glbfile={Discord} />
                    <OrbitControls enableZoom={false} autoRotate={false} />
                  </Suspense>
                </Canvas>
              </a>
            </Elements>
            <Elements>
              <a href="https://wa.me/918329811873?text='Hi, Please Type'">
                <Canvas camera={{ position: [0, 0, 2], fov: 25 }}>
                  <Suspense fallback={null}>
                    <ambientLight intensity={0.8} />
                    <directionalLight position={[10, 10, -10]} />
                    <Cube glbfile={Whatsapp} />
                    <OrbitControls enableZoom={false} autoRotate={false} />
                  </Suspense>
                </Canvas>
              </a>
            </Elements>
          </ElementContainer>
        </Left>
        <Right>
          <MapChart />
          <a href="#Home">
            <i class="fa fa-arrow-circle-up" style={{ fontSize: "25px" }}></i>
          </a>
        </Right>
      </Container>
    </Section>
  );
};

export default ContactMe;
