import React, { Suspense } from "react";
import styled from "styled-components";
import { OrbitControls } from "@react-three/drei";
import { Canvas } from "@react-three/fiber";
import Cube from "./Cube";
import blockchainback from "../asset/img/Blockchaineth.glb";
import Navbar from "./Navbar";
const Section = styled.div`
  height: 100vh;
  color: white;
  scroll-snap-align: start;
  display: flex;
  flex-direction: column;
  position: relative;
`;
const Container = styled.div`
  z-index: 1;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;
const Front = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
`;
const Back = styled.div`
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 0;
  align-self: center;
`;
const Title = styled.div`
  color: transparent;
  -webkit-text-stroke: 5px white;
  text-align: center;
  font-size: 10rem;
  font-weight: bold;
  line-height: 0.9em;
  @media only screen and (max-width: 768px) {
    font-size: 4rem;
    -webkit-text-stroke: 3px white;
  }
`;
const Desc = styled.div`
  text-align: center;
  font-size: 1.5rem;
  @media only screen and (max-width: 768px) {
    font-size: 1.3rem;
    width: 90%;
  }
`;

const Button = styled.button`
  display: block;
  width: 150px;
  font-size: 1.2rem;
  padding: 20px;
  cursor: pointer;
  text-align: center;
  outline: none;
  color: white;
  background-color: rebeccapurple;
  border: none;
  border-radius: 40px;
  box-shadow: 0 4px #999;
  &: hover {
    background-color: #7e3fbd;
  }

  &: active {
    background-color: #7e3fbd;
    box-shadow: 0 2px #666;
    transform: translateY(4px);
  }
`;

const HERO = () => {
  const width = window.innerWidth;
  const breakpoint = 600;

  return (
    <Section id="Home">
      <Navbar />
      <Container>
        <Front>
          <Title>DESIGNING EVOLUTION</Title>
          <br />
          <Desc>
            Hi, am <b>Aniket Andharikar</b> former Web2 developer eger to
            leverage Web2 experience
            <br /> while delving deeper into the world of Web3 creating
            impactful solutions
            <br /> in <b> DeFi</b>, <b>DAO</b> and <b>Web3</b>
            -oriented spaces.
          </Desc>
          <a href="#Contact">
            <Button>Contact Me</Button>
          </a>
        </Front>
      </Container>
      <Back>
        {width < breakpoint ? (
          <Canvas camera={{ position: [0, 0, 35], fov: 75 }}>
            <Suspense fallback={null}>
              <ambientLight intensity={0.8} />
              <directionalLight position={[10, 10, -10]} />
              <Cube glbfile={blockchainback} />
              <OrbitControls
                enableZoom={false}
                autoRotate
                autoRotateSpeed={1.2}
              />
            </Suspense>
          </Canvas>
        ) : (
          <Canvas camera={{ position: [0, 0, 25], fov: 75 }}>
            <Suspense fallback={null}>
              <ambientLight intensity={0.8} />
              <directionalLight position={[10, 10, -10]} />
              <Cube glbfile={blockchainback} />
              <OrbitControls
                enableZoom={false}
                autoRotate
                autoRotateSpeed={1.2}
              />
            </Suspense>
          </Canvas>
        )}
      </Back>
    </Section>
  );
};

export default HERO;
