import React from "react";
import {
  ComposableMap,
  Geographies,
  Geography,
  Annotation,
} from "react-simple-maps";
import geog from "../asset/features.json";
const MapChart = () => {
  return (
    <ComposableMap
      projection="geoAzimuthalEqualArea"
      projectionConfig={{
        rotate: [-80, -22, 0],
        center: [0, 0],
        scale: 1100,
      }}
    >
      <Geographies
        geography={geog}
        stroke="#FFFFFF"
        strokeWidth={0.5}
        fill="transparent"
      >
        {({ geographies }) =>
          geographies.map((geo) =>
            geo.id === "IND" ? (
              <Geography key={geo.rsmKey} geography={geo} fill="orange" />
            ) : (
              <Geography key={geo.rsmKey} geography={geo} />
            )
          )
        }
      </Geographies>
      <Annotation
        subject={[75.21, 19.54]}
        dx={-20}
        dy={-20}
        connectorProps={{
          stroke: "#FF0",
          strokeWidth: 3,
          strokeLinecap: "round",
        }}
      >
        <text
          x="-8"
          textAnchor="end"
          alignmentBaseline="middle"
          fill="#F0F0F0"
          fontSize={"20px"}
          fontWeight={"bold"}
        >
          {"Chhatrapati Sambhaji Nagar"}
        </text>
      </Annotation>
    </ComposableMap>
  );
};

export default MapChart;
